
<script context="module">

import { 
  VmIconLibrary 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-icon-library', VmIconLibrary);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let name = undefined;
export let resolver = undefined;
export let icons = 'material';

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'resolver', resolver);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-icon-library
  class={className}
  style={style}
  name={name}
  icons={icons}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-icon-library>
  