
<script context="module">

import { 
  VmVimeo,
  VmEmbed 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-vimeo', VmVimeo);
define('vm-embed', VmEmbed);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let videoId;
export let byline = true;
export let color = undefined;
export let portrait = true;
export let noAutoAspectRatio = false;
export let poster = undefined;
export let cookies = true;
export let language = 'en';
export let aspectRatio = '16:9';
export let autoplay = false;
export let controls = false;
export let logger = undefined;
export let loop = false;
export let muted = false;
export let playsinline = false;
export const getAdapter = (...args) => __ref.getAdapter(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'logger', logger);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-vimeo
  class={className}
  style={style}
  video-id={videoId}
  byline={byline}
  color={color}
  portrait={portrait}
  no-auto-aspect-ratio={noAutoAspectRatio}
  poster={poster}
  cookies={cookies}
  language={language}
  aspect-ratio={aspectRatio}
  autoplay={autoplay}
  controls={controls}
  loop={loop}
  muted={muted}
  playsinline={playsinline}
  on:vmLoadStart={onEvent}
  on:vmError={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-vimeo>
  