
<script context="module">

import { 
  VmPlaybackControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-playback-control', VmPlaybackControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let playIcon = 'play';
export let pauseIcon = 'pause';
export let icons = undefined;
export let tooltipPosition = 'top';
export let tooltipDirection = undefined;
export let hideTooltip = false;
export let keys = 'k';
export let paused = true;
export let i18n = {};

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-playback-control
  class={className}
  style={style}
  play-icon={playIcon}
  pause-icon={pauseIcon}
  icons={icons}
  tooltip-position={tooltipPosition}
  tooltip-direction={tooltipDirection}
  hide-tooltip={hideTooltip}
  keys={keys}
  paused={paused}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-playback-control>
  