
<script context="module">

import { 
  VmSpinner 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-spinner', VmSpinner);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let isVideoView = false;
export let currentProvider = undefined;
export let showWhenMediaLoading = false;
export let playbackReady = false;
export let buffering = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-spinner
  class={className}
  style={style}
  is-video-view={isVideoView}
  current-provider={currentProvider}
  show-when-media-loading={showWhenMediaLoading}
  playback-ready={playbackReady}
  buffering={buffering}
  on:vmWillShow={onEvent}
  on:vmWillHide={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-spinner>
  