
<script context="module">

import { 
  VmEmbed 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-embed', VmEmbed);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let embedSrc = '';
export let mediaTitle = '';
export let params = '';
export let origin = undefined;
export let preconnections = [];
export let decoder = undefined;
export const postMessage = (...args) => __ref.postMessage(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'preconnections', preconnections);
$: if (__mounted) setProp(__ref, 'decoder', decoder);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-embed
  class={className}
  style={style}
  embed-src={embedSrc}
  media-title={mediaTitle}
  params={params}
  origin={origin}
  on:vmEmbedSrcChange={onEvent}
  on:vmEmbedMessage={onEvent}
  on:vmEmbedLoaded={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-embed>
  