
<script context="module">

import { 
  VmMenuRadioGroup 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-menu-radio-group', VmMenuRadioGroup);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let value = undefined;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-menu-radio-group
  class={className}
  style={style}
  value={value}
  on:vmCheck={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-menu-radio-group>
  