
<script context="module">

import { 
  VmMuteControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-mute-control', VmMuteControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let lowVolumeIcon = 'volume-low';
export let highVolumeIcon = 'volume-high';
export let mutedIcon = 'volume-mute';
export let icons = undefined;
export let tooltipPosition = 'top';
export let tooltipDirection = undefined;
export let hideTooltip = false;
export let keys = 'm';
export let volume = 50;
export let muted = false;
export let i18n = {};

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-mute-control
  class={className}
  style={style}
  low-volume-icon={lowVolumeIcon}
  high-volume-icon={highVolumeIcon}
  muted-icon={mutedIcon}
  icons={icons}
  tooltip-position={tooltipPosition}
  tooltip-direction={tooltipDirection}
  hide-tooltip={hideTooltip}
  keys={keys}
  volume={volume}
  muted={muted}
  on:vmFocus={onEvent}
  on:vmBlur={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-mute-control>
  