
<script context="module">

import { 
  VmMenuRadio,
  VmIcon,
  VmMenuItem 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-menu-radio', VmMenuRadio);
define('vm-icon', VmIcon);
define('vm-menu-item', VmMenuItem);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let label;
export let value;
export let checked = false;
export let badge = undefined;
export let checkIcon = 'check';
export let icons = undefined;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-menu-radio
  class={className}
  style={style}
  label={label}
  value={value}
  checked={checked}
  badge={badge}
  check-icon={checkIcon}
  icons={icons}
  on:vmCheck={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-menu-radio>
  