
<script context="module">

import { 
  VmSlider 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-slider', VmSlider);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let step = 1;
export let min = 0;
export let max = 10;
export let value = 5;
export let valueText = undefined;
export let label = undefined;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-slider
  class={className}
  style={style}
  step={step}
  min={min}
  max={max}
  value={value}
  value-text={valueText}
  label={label}
  on:vmValueChange={onEvent}
  on:vmFocus={onEvent}
  on:vmBlur={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-slider>
  