
<script context="module">

import { 
  VmScrubberControl,
  VmSlider,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-scrubber-control', VmScrubberControl);
define('vm-slider', VmSlider);
define('vm-tooltip', VmTooltip);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let alwaysShowHours = false;
export let hideTooltip = false;
export let currentTime = 0;
export let duration = -1;
export let noKeyboard = false;
export let buffering = false;
export let buffered = 0;
export let i18n = {};

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-scrubber-control
  class={className}
  style={style}
  always-show-hours={alwaysShowHours}
  hide-tooltip={hideTooltip}
  current-time={currentTime}
  duration={duration}
  no-keyboard={noKeyboard}
  buffering={buffering}
  buffered={buffered}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-scrubber-control>
  