
<script context="module">

import { 
  VmSkeleton 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-skeleton', VmSkeleton);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let effect = 'sheen';
export let ready = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-skeleton
  class={className}
  style={style}
  effect={effect}
  ready={ready}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-skeleton>
  