
<script context="module">

import { 
  VmSubmenu,
  VmIcon,
  VmMenu,
  VmMenuItem 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-submenu', VmSubmenu);
define('vm-icon', VmIcon);
define('vm-menu', VmMenu);
define('vm-menu-item', VmMenuItem);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let label;
export let hint = undefined;
export let slideInDirection = 'right';
export let active = false;
export const getController = (...args) => __ref.getController(...args);
export const getMenu = (...args) => __ref.getMenu(...args);
export const getControllerHeight = (...args) => __ref.getControllerHeight(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-submenu
  class={className}
  style={style}
  label={label}
  hint={hint}
  slide-in-direction={slideInDirection}
  active={active}
  on:vmOpenSubmenu={onEvent}
  on:vmCloseSubmenu={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-submenu>
  