
<script context="module">

import { 
  VmCaptionControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-caption-control', VmCaptionControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let showIcon = 'captions-on';
export let hideIcon = 'captions-off';
export let tooltipPosition = 'top';
export let tooltipDirection = undefined;
export let hideTooltip = false;
export let icons = undefined;
export let keys = 'c';
export let i18n = {};
export let playbackReady = false;
export let textTracks = [];
export let isTextTrackVisible = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);
$: if (__mounted) setProp(__ref, 'textTracks', textTracks);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-caption-control
  class={className}
  style={style}
  show-icon={showIcon}
  hide-icon={hideIcon}
  tooltip-position={tooltipPosition}
  tooltip-direction={tooltipDirection}
  hide-tooltip={hideTooltip}
  icons={icons}
  keys={keys}
  playback-ready={playbackReady}
  is-text-track-visible={isTextTrackVisible}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-caption-control>
  