
<script context="module">

import { 
  VmPoster 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-poster', VmPoster);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let fit = 'cover';
export let isVideoView = false;
export let currentPoster = undefined;
export let mediaTitle = undefined;
export let playbackStarted = false;
export let currentTime = 0;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-poster
  class={className}
  style={style}
  fit={fit}
  is-video-view={isVideoView}
  current-poster={currentPoster}
  media-title={mediaTitle}
  playback-started={playbackStarted}
  current-time={currentTime}
  on:vmLoaded={onEvent}
  on:vmWillShow={onEvent}
  on:vmWillHide={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-poster>
  