
<script context="module">

import { 
  VmTimeProgress,
  VmCurrentTime,
  VmTime,
  VmEndTime 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-time-progress', VmTimeProgress);
define('vm-current-time', VmCurrentTime);
define('vm-time', VmTime);
define('vm-end-time', VmEndTime);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let separator = '/';
export let alwaysShowHours = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-time-progress
  class={className}
  style={style}
  separator={separator}
  always-show-hours={alwaysShowHours}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-time-progress>
  