
<script context="module">

import { 
  VmSettings,
  VmMenu 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-settings', VmSettings);
define('vm-menu', VmMenu);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let pin = 'bottomRight';
export let active = false;
export let isMobile = false;
export let isAudioView = false;
export const setController = (...args) => __ref.setController(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-settings
  class={className}
  style={style}
  pin={pin}
  active={active}
  is-mobile={isMobile}
  is-audio-view={isAudioView}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-settings>
  