                                <span class="genre borders p-1">{name}</span>
                            {/each}

                        <span class="time p-1">
                          {data.runtime}
                        </span>
                    
                    </div>


                    <div>
                        <h3 style=" 
                            font-size: 1.1em;
                            font-weight: 400;
                            font-style: italic;
                            opacity: 0.7;" class="text-start fst-italic">
                            {data.tagline}</h3>
                        <h3 class="text-start"></h3>
                        <p class="text-start">
                            {data.overview}</p>
                    </div>
              </div>
