
<script context="module">

import { 
  VmControl 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-control', VmControl);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let keys = undefined;
export let identifier = undefined;
export let hidden = false;
export let label;
export let menu = undefined;
export let expanded = undefined;
export let pressed = undefined;
export let isTouch = false;
export const focusControl = (...args) => __ref.focusControl(...args);
export const blurControl = (...args) => __ref.blurControl(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-control
  class={className}
  style={style}
  keys={keys}
  identifier={identifier}
  hidden={hidden}
  label={label}
  menu={menu}
  expanded={expanded}
  pressed={pressed}
  is-touch={isTouch}
  on:vmInteractionChange={onEvent}
  on:vmFocus={onEvent}
  on:vmBlur={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-control>
  