
<script context="module">

import { 
  VmControlGroup 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-control-group', VmControlGroup);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let space = 'none';

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-control-group
  class={className}
  style={style}
  space={space}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-control-group>
  