
<script context="module">

import { 
  VmFile 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-file', VmFile);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let willAttach = false;
export let crossOrigin = undefined;
export let preload = 'metadata';
export let poster = undefined;
export let mediaTitle = undefined;
export let controlsList = undefined;
export let autoPiP = undefined;
export let disablePiP = undefined;
export let disableRemotePlayback = undefined;
export let viewType = undefined;
export let playbackRates = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 2];
export let language = 'en';
export let autoplay = false;
export let controls = false;
export let logger = undefined;
export let loop = false;
export let muted = false;
export let playsinline = false;
export let noConnect = false;
export let paused = true;
export let currentTime = 0;
export let volume = 0;
export let playbackReady = false;
export let playbackStarted = false;
export let currentTextTrack = -1;
export let hasCustomTextManager = false;
export let isTextTrackVisible = true;
export let shouldRenderNativeTextTracks = true;
export const getAdapter = (...args) => __ref.getAdapter(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'playbackRates', playbackRates);
$: if (__mounted) setProp(__ref, 'logger', logger);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-file
  class={className}
  style={style}
  will-attach={willAttach}
  cross-origin={crossOrigin}
  preload={preload}
  poster={poster}
  media-title={mediaTitle}
  controls-list={controlsList}
  auto-pip={autoPiP}
  disable-pip={disablePiP}
  disable-remote-playback={disableRemotePlayback}
  view-type={viewType}
  language={language}
  autoplay={autoplay}
  controls={controls}
  loop={loop}
  muted={muted}
  playsinline={playsinline}
  no-connect={noConnect}
  paused={paused}
  current-time={currentTime}
  volume={volume}
  playback-ready={playbackReady}
  playback-started={playbackStarted}
  current-text-track={currentTextTrack}
  has-custom-text-manager={hasCustomTextManager}
  is-text-track-visible={isTextTrackVisible}
  should-render-native-text-tracks={shouldRenderNativeTextTracks}
  on:vmLoadStart={onEvent}
  on:vmError={onEvent}
  on:vmMediaElChange={onEvent}
  on:vmSrcSetChange={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-file>
  