

  <script>
    import {push, pop, replace} from 'svelte-spa-router'
    import { fade, fly, slide, scale, blur } from "svelte/transition"
    import * as eases from "svelte/easing"
    import Loading from './Loading.svelte'
    import Player from './Player.svelte'
    import { onMount } from 'svelte';

    // import Player from './Player.svelte'
    

    let currentEasing = eases.circIn
    let duration = 700
    
    export let params = {}
    let genres = []

    //let videos = []

    let data

    data = {
        files: [],
        loading : true
    }

    async function mediaURL(page) {
         try { 
            let res = await fetch('https:'+page);
            let filmsPage = await res.text();
            if(res.ok) {
                let parser = new DOMParser();
                let doc = parser.parseFromString(filmsPage, 'text/html');
                let files = doc.getElementById("files").value
                let jsonFilm = files.replaceAll('\\', '')
                data.files = jsonFilm
                let filmObject = JSON.parse(jsonFilm);
                let video = new Array

                Object.keys(filmObject).forEach((value, key, array) => {
                        let str = Object.getOwnPropertyDescriptor(filmObject,value)
                        let strValue = str.value
                        strValue = strValue.replaceAll(' or //', ',')
                        strValue = strValue.split(',')
                        let o = Object.create({}, { id: { value: value }, src: {value: strValue[13].slice(0, -7)} })
                        video.push(o)
                    }
                )
                return video
            }
        } catch (error) {
            console.error(':', error);
        }
    }

    async function loadDataFilm(id) {
        const response = await fetch("https://api.themoviedb.org/3/movie/"+id+"?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU");
        const character = await response.json()
        if(response.ok) {
            data.overview = character.overview
            data.poster_path = (character.poster_path !== null && character.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
            data.backdrop_path = (character.backdrop_path !== null && character.backdrop_path !== undefined)? 'https://image.tmdb.org/t/p/original'+character.backdrop_path : './build/backdrop.webp'
            data.tagline = character.tagline
            data.runtime = (character.runtime / 60 | 0) + "  " + character.runtime % 60 + " "
            // data.media = character.media
            genres = character.genres
        }
    }

    function module(page) {
            data.loading = true
            fetch('https://38.svetacdn.in/api/movies?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O&imdb_id='+page)
                .then(response => response.json())
                .then((function(result) {
                    data = result.data[0]
                    loadDataFilm(params.id)
                    document.title = ': '+data.ru_title
                    data.loading = false
                })
            )
    }

    //     async function module(page) {
    //         data.loading = true
    //         const res = await fetch('https://38.svetacdn.in/api/movies?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O&imdb_id='+page);
    //         const filmsPage = await res.json();
        
    //     if (res.ok) {
    //         data = filmsPage.data[0]
    //         loadDataFilm(params.id)
    //         document.title = ': '+data.ru_title
    //         console.log(data)
    //         mediaURL(data.media[0].path)
    //         data.loading = false
    //     } else {
    //         throw new Error(filmsPage);
    //     }
    // }
    module(params.id)
    let filmDate = (date,id) => {
        let data1 = new Date(date)
        if(id === 1) {data1 = data1.getFullYear(data1)}
        if(id === 2) {
            let year = data1.getFullYear(data1)
            let month = data1.getMonth(data1)+1
            let day = data1.getDay(data1)
            data1 = day+'/'+month+'/'+year
        }
        return data1
    }
</script>
{#if data.loading === true}
    <Loading />
{:else if data.loading !== true}
<div in:fade={{easing: currentEasing, duration}}>
    
    <header class="shadow-inner">

        <!-- Background image -->
        <div
          class="bg-image"
          style="
            background-image: url({data.backdrop_path});
            height: 500px
          "
        >
          <div class="mask" style="background:linear-gradient(
            45deg,
            rgba(79, 74, 58, 0.7),
            rgb(4%, 16%, 40%, 0.7) 100%
          )">

            <div class="d-flex h-100 container">
                <div class="d-flex py-3 h-100">
                    <!-- svelte-ignore a11y-missing-attribute -->
                    <img style="border-radius: 8px;" class="h-100 shadow-3" src={data.poster_path}>
                </div>

                <div class="d-flex flex-column w-100 text-white m-3">
                    <div class="d-flex">
                        <h2 >{data.ru_title}</h2>
                        <h2 class="fw-light"><span class="">&nbsp;({filmDate(data.year, 1)})</span></h2>
                    </div>
                    <div class="d-flex" style="

                        border-radius: 2px;
                        margin-left: 7px;
                        margin-right: 7px;">
                        <!-- <span class="border" style="

                            border-radius: 2px;
                            margin-right: 7px;">
                          R
                        </span> -->
                        <span class="p-1">
                            {filmDate(data.year, 2)}
                        </span>
                            {#each genres  as {id,name}, i}
                                <span class="genre borders p-1">{name}</span>
                            {/each}

                        <span class="time p-1">
                          {data.runtime}
                        </span>
                    
                    </div>


                    <div>
                        <h3 style=" 
                            font-size: 1.1em;
                            font-weight: 400;
                            font-style: italic;
                            opacity: 0.7;" class="text-start fst-italic">
                            {data.tagline}</h3>
                        <h3 class="text-start"></h3>
                        <p class="text-start">
                            {data.overview}</p>
                    </div>
              </div>
            </div>
          </div>
        </div>
      </header>
      <main class="container mt-5 mb-5">
        <!-- {#each data.videos as {id, src}}
            <Player src = {src} poster = {data.backdrop_path}/>
        {/each} -->
        
        <Player videos = {data.media[0].path} poster = {data.backdrop_path} resolution = {data.media[0].max_quality}/>
      </main>
</div>
{/if}