
<script context="module">

import { 
  VmDblClickFullscreen 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-dbl-click-fullscreen', VmDblClickFullscreen);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let useOnMobile = false;
export let isFullscreenActive = true;
export let isVideoView = false;
export let playbackReady = false;
export let isMobile = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-dbl-click-fullscreen
  class={className}
  style={style}
  use-on-mobile={useOnMobile}
  is-fullscreen-active={isFullscreenActive}
  is-video-view={isVideoView}
  playback-ready={playbackReady}
  is-mobile={isMobile}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-dbl-click-fullscreen>
  