
<script context="module">

import { 
  VmLoadingScreen 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-loading-screen', VmLoadingScreen);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let playbackReady = false;
export let hideDots = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-loading-screen
  class={className}
  style={style}
  playback-ready={playbackReady}
  hide-dots={hideDots}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-loading-screen>
  