
<script context="module">

import { 
  VmSettingsControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-settings-control', VmSettingsControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let icon = 'settings';
export let icons = undefined;
export let tooltipPosition = 'top';
export let tooltipDirection = undefined;
export let menu = undefined;
export let expanded = false;
export let i18n = {};
export const focusControl = (...args) => __ref.focusControl(...args);
export const blurControl = (...args) => __ref.blurControl(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-settings-control
  class={className}
  style={style}
  icon={icon}
  icons={icons}
  tooltip-position={tooltipPosition}
  tooltip-direction={tooltipDirection}
  menu={menu}
  expanded={expanded}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-settings-control>
  