<script>
    import {link} from 'svelte-spa-router'
    import {wrap} from 'svelte-spa-router/wrap'
    import Route from 'svelte-spa-router'
    import active from 'svelte-spa-router/active'
    import {push, pop, replace} from 'svelte-spa-router'
    import Films from './Films.svelte'

    const routes = {
        '/' : wrap({
            asyncComponent: () => import('./Films.svelte'),
            props: {
                params: {
                    id:1
                }
            }
        }),
        '/:id' : wrap({
            asyncComponent: () => import('./Films.svelte'),
        }),
        '/film/:id': wrap({
            asyncComponent: () => import('./Film.svelte'),
        }),
        '/film/id/:id': wrap({
            asyncComponent: () => import('./FilmCDN.svelte'),
        }),
        '/search/:text/:page': wrap({
            asyncComponent: () => import('./Search.svelte'),
        }),
        '*' : wrap({
            asyncComponent: () => import('./NotFound.svelte'),
        })
        
    }

    $: search = ''

    function searchFun(string) {
        search = ''
        if(string.length < 3) {
            console.log(string.length)
        } else {
            replace('/search/'+string+'/1')
        }
    }
</script>
<nav class="navbar navbar-light bg-light">
    <div class="container-fluid d-flex justify-content-center align-items-center">
        <div class="d-flex">
            <div class="input-group">
                <div class="form-outline">
                <input on:submit bind:value={search} type="search" id="form1" class="form-control bg-white" />
                <label class="form-label" for="form1">Search  3- </label>
                </div>
                <button on:click={searchFun(search)} type="button" class="btn btn-primary">
                <i class="fas fa-search"></i>
                </button>
            </div>
        </div>
            <button class="btn mx-2"  on:click={() => pop()}></button>
            <button class="btn mx-2" on:click={() => push('/')}> </button>
    </div>
</nav>
<Route {routes} restoreScrollState={true}/>