<script>
    import {link} from 'svelte-spa-router'
    import active from 'svelte-spa-router/active'
    import { onMount } from 'svelte';

    import { fade, fly, slide, scale, blur } from "svelte/transition";
    import * as eases from "svelte/easing";
    import { each } from 'svelte/internal';
    import Pagination from './Pagination.svelte'
    import Loading from './Loading.svelte'

    let currentEasing = eases.circIn
    let duration = 700

    let data = []
    data.loading = true

    export let params 

    let currentPage = 1


    console.log(params)
    $: mysearch = params.text
    async function searchFilm(text) {
        const response = await fetch('https://api.themoviedb.org/3/search/movie?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU&page=1&include_adult=false&query='+text);
        const character = await response.json()
        console.log('Search: ',character)
    }



    async function loadPoster(id,i) {
        const res = await fetch("https://api.themoviedb.org/3/movie/"+id+"?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU");
        const character = await res.json()

        if(res.ok && i !== undefined) {
            console.log('I : ', i)
            i.genres = character.genres
            // i.genres = (i.genres)? character.genres : ''
            const src = (character.poster_path !== null && character.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
            return src 
        } else {
            if(i !== undefined){
                const src = (character.poster_path !== null && character.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
                i.genres = [{id:1,name: ''}]
                return src 
            }
        }
    }
    async function module(text, page) {
        data.loading = true
        const res = await fetch('https://38.svetacdn.in/api/movies?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O&query='+text+'&page='+page);
        const filmsPage = await res.json();
        
        if (res.ok) {
            data = filmsPage.data
            data.total = filmsPage.total
            data.current_page = filmsPage.current_page
            data.last_page = filmsPage.last_page
            data.every((element, index, array) => loadPoster(data[index].imdb_id,element).then((returns)=>data[index].poster_path=returns))
            data.loading = false
            console.log('FilmsData: ', data)
        } else {
            throw new Error(filmsPage);
        }
    }
    $: module(mysearch, params.page)

    console.log(data)

    let filmDate = (date) => {
        let data1 = new Date(date)
        data1 = data1.getFullYear(data1)
        return data1
    }
</script>



{#if data.loading === true}
<Loading />
{:else if data.loading !== true}
    <div class="mt-3 d-flex justify-content-center">
        <h3> : {data.total}</h3>
    </div>

    <div class="m-2 container-fluid d-flex justify-content-center align-items-center">
        <Pagination text = {'search/'+params.text+'/'} allPages = {data.last_page} currentPage = {data.current_page}/>
    </div>
    <div class="wrapper container mt-2">
        {#each data as film}
            <div class="card d-flex flex-column m-2 " in:fade={{easing: currentEasing, duration}}>
                <div class="bg-image hover-overlay">
                    <img
                        class="card-img-top"
                        src={film.poster_path} alt="Poster" />
                        <span class="badge bg-warning shadow-2 position-absolute bottom-0 end-0 m-2 text-uppercase">{film.media[0].source_quality+' '+film.media[0].max_quality}</span>

                    <div
                        class="mask"
                        style="
                        background: linear-gradient(
                            45deg,
                            rgba(29, 236, 197, 0.5),
                            rgba(91, 14, 214, 0.5) 100%
                        );
                        "
                    ><a href="/film/{film.imdb_id}" use:link><i class="fas fa-play fa-4x text-primary d-flex align-items-center justify-content-center h-100"></i></a></div>
                </div>
                    
                <div class="p-2">
                    <a href="/film/{film.imdb_id}" use:link style="
                    overflow: hidden;
                    max-height: 54px;
                    margin: 5px 0 0;
                    font-size: 15px;
                    font-weight: 600;
                    line-height: 18px;
                    color: #333;
                    ">
                        <h3 style="
                        overflow: hidden;
                        max-height: 54px;
                        margin: 5px 0 0;
                        font-size: 15px;
                        font-weight: 600;
                        line-height: 18px;
                        color: #333;">
                            {film.ru_title}
                        </h3>
                    </a>
                    <div class="" style="
                    height: 18px;
                    margin: 2px 0 3px;
                    color: #a6a6a6;
                    font-size: 12px;
                    font-weight: 400;
                    line-height: 18px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;">
        
                        <span class="video-year">{filmDate(film.released)}</span>
                        {#each film.genres || [] as genre, i}
                            <span class="video-genres"> {genre.name} </span>
                            {:else} <!--  -->
                            <span>No items found</span>
                        {/each}
                    </div>
                </div>
            </div>
        {/each}
        </div>
        <div class="m-2 container-fluid d-flex justify-content-center align-items-center">
            <Pagination text = {'search/'+params.text+'/'} allPages = {data.last_page} currentPage = {data.current_page}/>
        </div>
        
        <!--     
        <a href="/hello/user" use:link use:active={{path: '/hello/*', className: 'active', inactiveClassName: 'inactive'}}>Say hi!</a>
        <a href="/hello/user" use:link use:active={'/hello/*'}>Say hi with a default className!</a>
        <a href="/hello/user" use:link use:active>Say hi with all default options!</a> -->
{/if}
