
<script context="module">

import { 
  VmMenuItem,
  VmIcon 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-menu-item', VmMenuItem);
define('vm-icon', VmIcon);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let identifier = undefined;
export let hidden = false;
export let label;
export let menu = undefined;
export let expanded = undefined;
export let checked = undefined;
export let hint = undefined;
export let badge = undefined;
export let checkIcon = 'check';
export let icons = undefined;
export let isTouch = false;
export const focusItem = (...args) => __ref.focusItem(...args);
export const blurItem = (...args) => __ref.blurItem(...args);
export const getHeight = (...args) => __ref.getHeight(...args);
export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'menu', menu);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-menu-item
  class={className}
  style={style}
  identifier={identifier}
  hidden={hidden}
  label={label}
  expanded={expanded}
  checked={checked}
  hint={hint}
  badge={badge}
  check-icon={checkIcon}
  icons={icons}
  is-touch={isTouch}
  on:vmFocus={onEvent}
  on:vmBlur={onEvent}
  bind:this={__ref}
>
  <slot></slot>
</vm-menu-item>
  