
<script context="module">

import { 
  VmTime 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-time', VmTime);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let label;
export let seconds = 0;
export let alwaysShowHours = false;

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});



const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-time
  class={className}
  style={style}
  label={label}
  seconds={seconds}
  always-show-hours={alwaysShowHours}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-time>
  