
<script context="module">

import { 
  VmLiveIndicator 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-live-indicator', VmLiveIndicator);
  
</script>

<script>
import { createEventDispatcher, onMount } from 'svelte';
import { setProp } from '../lib';

let __ref;
let __mounted = false;

const dispatch = createEventDispatcher();

let className = '';

export let style = '';
export { className as class };

export let isLive = false;
export let i18n = {};

export const ref = () => __ref;
export const getWebComponent = () => __ref;

onMount(() => {
  

  __mounted = true;
});

$: if (__mounted) setProp(__ref, 'i18n', i18n);

const onEvent = (e) => {
  e.stopPropagation();
  dispatch(e.type, e.detail);
};
</script>

<vm-live-indicator
  class={className}
  style={style}
  is-live={isLive}
  
  bind:this={__ref}
>
  <slot></slot>
</vm-live-indicator>
  